import React from "react";
import Dummy from "../db/data.json";
import { useParams } from "react-router-dom";
import Word from "./Word";

const Day = () => {
  //dummy.words
  const { day } = useParams();
  const wordList = Dummy.words.filter((word) => word.day === Number(day));
  //   console.log(wordList);

  return (
    <table>
      <h2>DAY {day}</h2>
      {wordList.map((word) => (
        <Word word={word} />
      ))}
    </table>
  );
};

export default Day;
