import React from "react";
import { Link } from "react-router-dom";
import Dummy from "../db/data.json";

const DayList = () => {
  console.log(Dummy);
  return (
    <ul className="list_day">
      {Dummy.days.map((day) => (
        <li key={day.id}>
          <Link to={`/day/${day.day}`}>Day {day.day}</Link>
        </li>
      ))}
      <li></li>
    </ul>
  );
};

export default DayList;
